<template>
  <div id="user-center">
    <div class="box1-left">
      <currentLocate></currentLocate>
    </div>
    <keep-alive>
      <router-view></router-view>
    </keep-alive>
  </div>
</template>

<script>
import currentLocate from "@/components/common/tools/currentLocate";
export default {
  components: {
    currentLocate
  },
};
</script>

<style lang="scss" scoped>
#user-center {
  height: 100%;
  width: 1200px;
  /* padding: 50px; */
  margin: 10px auto;
}
.box1-left {
  width: 100%;
  margin: 0 auto;
  height: 100%;
  overflow: hidden;
  border-radius: 2px;
}
</style>



